 {\n  font: inherit;\n  font-size: 100%;\n  width: 150px;\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\nimg {\n  height: 500px;\n  width: auto;\n}\n\nheader {\n  display: flex;\n  justify-content: space-between;\n}\n\nul {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\ninput[type='checkbox'] {\n  appearance: none;\n  height: 1.3rem;\n  min-height: 1.3rem;\n  width: 1.3rem;\n  min-width: 1.3rem;\n  margin: 0;\n  padding: 0;\n  border: solid var(--grey) 2px;\n  border-radius: 100px;\n  position: relative;\n  background-color: transparent;\n  cursor: pointer;\n}\n\ninput[type='checkbox']:checked {\n  background-color: var(--grey);\n}\n\ninput[type='checkbox']:checked::after {\n  content: \"\";\n  position: absolute;\n  left: 0.4rem;\n  top: 0.15rem;\n  width: 0.3rem;\n  height: 0.6rem;\n  border: solid var(--white);\n  border-width: 0 2px 2px 0;\n  transform: rotate(45deg);\n}\n\ntextarea {\n  resize: none;\n  width: 100%;\n  padding: 0px 5px 0px 5px;\n}\n\n.bold {\n  font-weight: 600;\n}\n\n.remove-border-background {\n  background: none;\n  border: none;\n}\n\n.pointer {\n  cursor: pointer;\n}\n\n.layout {\n  display: flex;\n}\n\n.sidebar {\n  position: fixed;\n  height: 100%;\n  width: 100%;\n  max-width: 350px;\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  color: var(--black);\n  background-color: var(--uranian-blue);\n  font-size: clamp(0.9rem, 1.5vw, 1.4rem);\n  border-right: solid 4px var(--black);\n  z-index: 100;\n}\n\nnav {\n  height: 80%;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}\n\n.logo-wrapper {\n  display: flex;\n  justify-content: center;\n  position: relative;\n  height: clamp(6rem, 14vh, 125px);\n  padding: 10px;\n}\n\n.logo {\n  height: 100%;\n  width: auto;\n}\n\n.horizontal-divider,\n.vertical-divider {\n  background-color: var(--black);\n  border-radius: 20px;\n}\n\n.horizontal-divider {\n  height: 4px;\n}